<template>
    <div id="cv">
        <div class="backgroundAll"></div>
        <div class="MainSideBar">
            <div class="detail">
                <div class="picture">
                    <img src="../assets/95385.jpg" alt="Vue.js PWA">
                </div>
                <div class="name">Rio Gunawan</div>
            </div>
            <div class="list">
                <ul>
                    <li :class="activeTab == 'introductions'? 'isActiveTab' : ''" @click.prevent="goto('introductions')"><p>Introduction</p></li>
                    <li :class="activeTab == 'skills'? 'isActiveTab' : ''" @click.prevent="goto('skills')"><p>Skills</p> </li>
                    <li :class="activeTab == 'software'? 'isActiveTab' : ''" @click.prevent="goto('software')"><p>Software Develpoment</p> </li>
                    <li :class="activeTab == 'organizations'? 'isActiveTab' : ''" @click.prevent="goto('organizations')"><p>Organization</p></li>
                    <!-- <li :class="activeTab == 'works'? 'isActiveTab' : ''" @click.prevent="goto('works')"><p>Work</p></li> -->
                </ul>
            </div>
            <div class="footer">
                <div class="medsos">
                    <img src="../assets/fb-blackwhite.svg" alt="fb">
                    <img src="../assets/ig-blackwhite.svg" alt="ig">
                    <img src="../assets/yt-blackwhite.svg" alt="yt">
                    <img src="../assets/tw-blackwhite.svg" alt="tw">
                    <img src="../assets/in-blackwhite.svg" alt="in">
                    <img src="../assets/wa-blackwhite.svg" alt="wa">
                </div>
            </div>
        </div>
        <div class="content">
          <div class="backgroundContent">
            <router-view></router-view>
          </div>
        </div>
    </div>
</template>
<script>
export default {
  data () {
    return {
      activeTab: 'introductions'
    }
  },
  methods: {
    goto (name) {
      this.activeTab = name
      this.$router.push({name: name})
    }
  }
}
</script>


